import Card from "./Elements/Card";
const generateCSS = (width, sumPost, postPosition) => {
    const smallDivWidth = `${width}%`;
    const floatDirection = sumPost === '1' ? (postPosition === 'right' ? 'left' : 'right') : (postPosition === 'right' ? 'left' : 'right');
    return { width: smallDivWidth, float: floatDirection };
}

const CollageRows = ({ wall, largeDiv, smallDiv, itemData, completeDataObject, parentID, itemIndex, clickToShowPopUp, onClickToCTA, postSizeInPx, adjustWidth, onClickPopUpSlider }) => {
    const postPosition = wall.ThemeRule.highlightPosition; const sumPost = itemIndex % 2;
    const largeDivStyle = { width: largeDiv + `%`, float: sumPost == '0' ? (postPosition == 'right' ? 'right' : 'left') : postPosition == 'right' ? 'left' : 'right' };
    const smallDivStyle = { width: smallDiv + `%`, float: sumPost == '1' ? (postPosition == 'right' ? 'left' : 'right') : postPosition == 'right' ? 'left' : 'right' }

    return (itemData && itemData.length > 0) ? <div className="ts_cs_post_row" style={{ width: `100%` }}>
        <div className="ts_cs_post_large" style={largeDivStyle}><Card ownerId={wall.Wall.owner} itemData={completeDataObject[itemData[0]]} key={`sqr_crd_${0}_${completeDataObject[itemData[0]].id}`} itemIndex={itemIndex} adjustWidth={100} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} completeDataObject={completeDataObject} onClickPopUpSlider={onClickPopUpSlider} /></div>
        <div className="ts_cs_post_small" style={smallDivStyle}>
            {
                (itemData && itemData.length > 0) ? itemData.map((item, index) => {
                    const cardData = completeDataObject[item];
                    return (index != 0 ? <Card ownerId={wall.Wall.owner} itemData={cardData} key={`sqr_crd_${index}_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} completeDataObject={completeDataObject} onClickPopUpSlider={onClickPopUpSlider} /> : null)
                }) : null
            } </div>

    </div> : null
}

export default CollageRows