import React, { Suspense, useRef, useEffect } from "react";
import { IS_THEME, PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import { suiteTrack } from '../../../../actions/api'
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
const AuthorDetails = React.lazy(() => import("./AuthorDetails"));
const Media = React.lazy(() => import('./Media'));
const PostContent = React.lazy(() => import('../../Elements/PostContent/PostContent'));

const trim_content_length = !isTagEmbed ? 200 : null;


const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const mediaType = itemData.type !== 1 ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  useEffect(() => {
    if (suiteTrack) themeImpressionTracking(postTrack);
  }, []);

  const isHotspot = itemData.hotspot && itemData.ugc_products.length > 0 ? true : false
  const isShopBtn = isTagShop && (!itemData.hotspot && itemData.ugc_products.length > 0) ? true : false;
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return <div id={`tb-sp-post-${itemData.id}`} className={`tb_ct_post_wrapper${responsiveCardSize2x ? ' tb_ct_post_wrapper2x' : ''}`} style={{ width: `${adjustWidth}%`, padding: personalization.padding / 2, float: 'left' }} tb-network={itemData.network.id} tb-product={isShopBtn ? 1 : isHotspot ? 2 : 0} tabIndex="0" role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId}>
    <div className="tb_ct_post_in" onClick={clickToShowPopUp(itemIndex, itemData)}>
      <div className="tb_ct_post_media_wrapp">
        {itemData.share.status && !isTagShop ? <ShareOnHover share={itemData.share} shareClass={'tb_ct_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : ''}
        {mediaType ? <Suspense fallback={<></>}><Media key={`img${itemIndex}_${itemData.id}`} itemData={{ ...itemData, postFileNew: itemData.filename }} wallID={wallID} ownerId={ownerId} ThemeID={personalization.widgetTheme} wall={wall} /></Suspense> : ''}
        {!isHotspot ? <>
          <div className="tb_ct_post_details">
            {isTagShop ? <div className="tb_ct_social_">
              <Network networkClass={'tb_ct_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} />
              {isShopBtn ? <ShopButton ThemeRule={ThemeRule} btnClass={'tb_ct_btn_wrap'} /> : null}
            </div> : null}
          </div>
          <div className={`tb_ct_post_overlay tb_ct_post_overlay-${themeMode}`}><div></div></div>
        </> : null}
        {/* new code */}

        {IS_THEME(68) ? <>
          <div className="tb_sp_contant_wrapper">
            {((!ThemeRule.hideContent || itemData.type == 1)) ? <Suspense fallback={<></>}><PostContent contentClass={'tb_sp_content'} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /></Suspense> : ''}
          </div>
          <Suspense fallback={<></>}><AuthorDetails key={`img${itemIndex}_${itemData.id}`} ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} /></Suspense>
        </> : null}


      </div>
    </div>
  </div>
}
export default Card