import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../actions/themeActions';
import "./scss/CollageTheme.scss";
import CollageRows from "./CollageRows";
export const getPostWidth = (windowWidth, ColumnCount) => {
  const getWidths = (inWidth, cardWidth) => ({ cardWidth, largeDiv: inWidth * 2, smallDiv: 100 - inWidth * 2 });

  switch (ColumnCount) {
    case 3: return getWidths(windowWidth > 320 ? 100 / 3 : 100 / 3, 100 / 1);
    case 5: return getWidths(windowWidth > 767 ? 100 / 4 : 100 / 3, windowWidth > 767 ? 100 / 2 : 100 / 1);
    case 7: return getWidths(windowWidth > 767 ? 100 / 5 : 100 / 3, windowWidth > 767 ? 100 / 3 : 100 / 1);
    case 9: return getWidths(windowWidth > 767 ? 100 / 6 : 100 / 3, windowWidth > 767 ? 100 / 4 : 100 / 1);
    default: return getWidths(windowWidth < 767 ? 100 / 3 : 100 / 1, 100 / 1);
  }
}
class CollageTheme extends PureComponent {
  state = {
    postData: [],
    windowWidth: window.innerWidth
  }
  componentDidMount() {
    const { renderId } = this.props;

    if (renderId.renderId) {
      this.setState({
        windowWidth: document.getElementById(renderId.renderId).clientWidth
      })
    }
  }
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, cardNumber, onClickToCTA, postSizeInPx, onClickPopUpSlider } = this.props;
    const { windowWidth } = this.state
    let totalPostBestFit = postData.length;
    if (wall && wall.Personalization.fitRow == 1) {
      if (postData.length >= parseInt(cardNumber)) {
        let bestFitEachRow = postData.length / parseInt(cardNumber);
        totalPostBestFit = (parseInt(cardNumber) * parseInt(bestFitEachRow));
      }
    }
    var clm = ''
    const ColumnCount = wall.ThemeRule.numberOfCoumn
    const { cardWidth, largeDiv, smallDiv } = getPostWidth(windowWidth, ColumnCount)
    if (windowWidth < 768) clm = 3
    else clm = ColumnCount ? ColumnCount : 1
    var i = 0, n = postData.length, newPostdata = [];
    while (i < n) { newPostdata.push(postData.slice(i, i += clm)); }
    var postClmnNo = windowWidth < 768 ? '3' : ColumnCount
    return (
      <div tabIndex="0" aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed" className="tb_ct_post_container">
        {(newPostdata && newPostdata.length > 0) && newPostdata.map((item, index) => {
          return item.length == postClmnNo ? <CollageRows ownerId={wall.Wall.owner} itemData={item} key={`sqr_crd_${index}_${item.id}`} itemIndex={index} adjustWidth={cardWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} completeDataObject={completeDataObject} largeDiv={largeDiv} smallDiv={smallDiv} onClickPopUpSlider={onClickPopUpSlider} /> : null
        })}
      </div>
    );
  }
}


const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}

const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CollageTheme)